.navbar {
  height: 100%;
}

.nav-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  display: flex;
  max-width: 130px;
  height: 100%;
}

.logo img {
  width: 100%;
  padding: 10px 0;
  margin-left: -18px;
}

.list-wrapper {
  display: flex;
  align-items: center;
}

.list-wrapper ul {
  top: 0;
  left: -100vw;
  z-index: 3;
  width: 100%;
  height: 86vh;
  padding-top: 140px;
  text-align: center;
  position: absolute;
  transition: all 0.5s ease-in-out;
  background-color: rgba(0, 0, 0, 0.8);
}

.list-wrapper ul li {
  list-style: none;
  margin-bottom: 15px;
}

.list-wrapper ul li a {
  display: block;
  font-size: 24px;
  padding: 10px 0;
  color: #dddddd;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
}

.list-wrapper ul li a:hover {
  color: #000000;
  background-color: #dddddd;
}

.dark .list-wrapper ul li a:hover {
  color: white;
  background-color: #dddddd;
}

.list-wrapper img {
  opacity: 1;
  z-index: 4;
  width: 40px;
  right: 15px;
  padding: 8px;
  position: absolute;
  border-radius: 999px;
  background-color: #dddddd;
  transition: all 0.5s ease-in-out;
}

.switch {
  width: 80px;
  height: 40px;
  margin-top: 10px;
  float: right;
  border-radius: 20px;
  background-color: #cc3300;
  position: relative;
}

.flicker {
  width: 30px;
  height: 30px;
  border-radius: 15px;
  background-color: rgb(255, 255, 255);
  position: absolute;
  top: 5px;
  left: 5px;
  box-shadow: inset 4px 4px 4px rgb(255, 255, 255),
    inset -4px -4px 4px rgb(195, 193, 198);
  transition: 0.3s;
}

.dark {
  background-color: #cc3300;
  /* background-color: white; */
  color: white;
  transition: 0.3s;
}

.moon {
  width: 20px;
  height: 20px;
  border-radius: 10px;
  box-shadow: 4px 4px 0 0 rgb(255, 255, 255);
  position: absolute;
  top: 5px;
  left: 45px;
}

.dark .flicker {
  transform: translateX(40px);
}

@media (min-width: 800px) {
  .list-wrapper img {
    display: none;
  }

  .list-wrapper ul {
    padding: 0;
    width: 100%;
    height: 100%;
    text-align: left;
    position: relative;
    background-color: transparent;
  }
  .list-wrapper ul li {
    display: inline-block;
    margin-bottom: 0;
  }
  .list-wrapper ul li:not(:last-child) {
    margin-right: 10px;
  }
  .list-wrapper ul li a {
    padding: 5px;
    color: #000000;
  }

  .dark .list-wrapper ul li a {
    padding: 5px;
    color: white;
  }

  .list-wrapper ul li a:hover {
    padding: 5px;
    color: #333333;
    transform: scale(0.8);
    background: transparent;
  }

  .dark .list-wrapper ul li a:hover {
    padding: 5px;
    color: white;
    transform: scale(0.8);
    background: transparent;
  }
}
